:root {
    --Lime-Green: hsl(136, 65%, 51%);
    --Bright-Cyan: hsl(192, 70%, 51%);
    --Grayish-Blue: hsl(233, 8%, 62%);
    --White: hsl(0, 0%, 100%);
}

.header {
    width: 100%;
    background-color: var(--White);
    position: relative;
    z-index: 3;
}

.container {
    width: 90%;
    height: 70px;
    line-height: 70px;
    margin: 0 auto;
    display: flex;
}

.header .logo {
    width: 25%;
    margin: 0 auto 0 0;
    cursor: pointer;
}

.header ul {
    width: 50%;
    display: flex;
    margin: 0 auto;
}

.header ul li {
    width: calc(70% /5);
    margin: 0 auto;
    color: var(--Grayish-Blue);
    cursor: pointer;
    text-align: center;
}

.header ul li:hover {
    border-bottom: 3px solid var(--Lime-Green);
}

.header .request {
    width: 25%;
    margin: 0 auto;
    text-align: center;
}

.header .request button {
    width: 60%;
    padding: 15px;
    margin: 0 auto;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 20px;
    color: var(--White);
    background-image: linear-gradient(to right, var(--Lime-Green), var(--Bright-Cyan));
}
.header .hamburger {
    display: none;
}
.header .close {
    display: none;
}
@media (max-width: 375px) {
    .header {
        width: 100%;
        height: 10%;
    }
    .header ul {
        flex-direction: column;
        position: absolute;
        width: 90%;
        top: 115%;
        left: 5%;
        background-color: var(--White);
        border-radius: 10px;
        display: none;
    }
    .header ul::after {
        content: '';
        position: absolute;
        width: 100vw;
        height: 100vh;
        top: 0;
        left: -20px;
        right: 0;
        bottom: 0;
        background-color: #00000024;
        z-index: -1;
    }
    .header ul li {
        padding: 10px 0;
    }
    .header button {
        display: none;
    }
    .header .hamburger {
        display: inline-block;
        position: absolute;
        top: 25px;
        right: 20px;
    }
    .header .close {
    position: absolute;
    top: 20px;
    right: 25px;
    }
}