:root {
    --Dark-Blue: hsl(233, 26%, 24%);
    --Grayish-Blue: hsl(233, 8%, 62%);
    --Light-Grayish-Blue: hsl(220, 16%, 96%);
    --Very-Light-Gray: hsl(0, 0%, 98%);
}

.about {
    background-color: var(--Light-Grayish-Blue);
    margin-top: 25%;
    padding: 5% 0;
}

.about .heading {
    padding: 10% 5%;
    text-align: center;
}

.about .heading h2 {
    width: 70%;
    margin: auto auto 25px;
    color: var(--Dark-Blue);
    font-size: 30px;
    font-weight: 300;
}

.about p {
    width: 95%;
    color: var(--Grayish-Blue);
    font-size: 18px;
    margin: auto auto;
    line-height: 1.6;
}

.about .services .service {
    width: 90%;
    margin: 8% auto;
    text-align: center;
}

.about .services .service h3 {
    margin: 10%;
    color: var(--Dark-Blue);
    font-weight: 400;
}

@media (min-width:380px) {
    .about {
        margin-top: 0;
        padding: 0;
    }
    .about .heading {
        text-align: left;
        padding: 5%;
    }
    .about .heading h2 {
        width: 70%;
        margin: 0 0 25px;
    }
    .about .heading p {
        width: 60%;
        margin: 0 auto 0 0;
    }
    .about .services {
        display: flex;
        width: 90%;
        margin: auto;
    }
    .about .services .service {
        width: calc(100% /4);
        margin-top: 0;
        margin-bottom: 3%;
        text-align: left;
    }
    .about .services .service h3 {
        margin: 3%;
    }
    .about .services .service img {
        width: 55px;
        margin-left: 10px;
    }
}