:root {
    --Dark-Blue: hsl(233, 26%, 24%);
    --Grayish-Blue: hsl(233, 8%, 62%);
    --Very-Light-Gray: hsl(0, 0%, 98%);
    --White: hsl(0, 0%, 100%);
}

.landing {
    background-color: var(--Very-Light-Gray);
    height: 100%;
}
.landing .head {
    width: 90%;
    margin:0 auto ;
    
}
.landing .head h3 {
    padding: 3% 0;
    color: var(--Dark-Blue);
    font-size: 30px;
    font-weight: 300;
}
.landing .articales {
    width: 92%;
    margin: auto;
    display: flex;
}
.landing .articale {
    width: calc(90% / 4);
    height: 550px;
    margin: auto;
    margin-bottom: 3%;
    background-color: var(--White);
    border-radius: 10px;
}
.landing .articale img {
    width: 100%;
    height: 50%;
    border-radius: 10px 10px 0 0;
}
.landing .articale .text {
    width: 90%;
    margin: 3% 5%;
}
.landing .articale h4 {
    color: var(--Grayish-Blue);
    font-weight: 300;
}
.landing .articale h3 {
    margin: 5% 0;
    color: var(--Dark-Blue);
    font-size: 20px;
    font-weight: 400;
    cursor: pointer;
}
.landing .articale h3:hover {
  color: #8dcbb6;
}
.landing .articale p {
    color: var(--Grayish-Blue);
    font-size: 18px;
    line-height: 1.6;
}
@media (max-width: 375px) {
    .landing .head {
        text-align: center;
        padding: 15% 0 5%;
    }
    .landing .articales {
        flex-direction: column;
        width: 100%;
    }
    .landing .articale {
        width: 90%;
        height: 500px;
        padding-bottom: 8%;
        margin: 3% auto;
    }
}