:root {
    --Dark-Blue: hsl(233, 26%, 24%);
    --Lime-Green: hsl(136, 65%, 51%);
    --Bright-Cyan: hsl(192, 70%, 51%);
    --Grayish-Blue: hsl(233, 8%, 62%);
    --Light-Grayish-Blue: hsl(220, 16%, 96%);
    --Very-Light-Gray: hsl(0, 0%, 98%);
    --White: hsl(0, 0%, 100%);
}

.home {
    width: 100%;
    height: 600px;
    display: flex;
    overflow-x: clip;
}

.home .title {
    width: 40%;
    margin: auto;
    margin-left: 5%;
}

.home .title h2 {
    color: var(--Dark-Blue);
    margin-bottom: 25px;
    font-size: 50px;
    font-weight: 300;
}

.home .title p {
    width: 90%;
    color: var(--Grayish-Blue);
    font-size: 18px;
    margin-bottom: 25px;
    line-height: 1.6;
}

.home .title button {
    width: 50%;
    padding: 15px;
    margin: 0 auto;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 20px;
    color: var(--White);
    background-image: linear-gradient(to right, var(--Lime-Green), var(--Bright-Cyan));
}

.home .image {
    background-image: url('../../../public/images/bg-intro-desktop.svg');
    background-repeat: no-repeat;
    background-size: 125%;
    background-position: -10% 70%;
    width: 55%;
    position: relative;
}

.home .image .mockups {
    width: 100%;
    position: absolute;
    top: -15%;
    left: 15%;
    z-index: 2;
}

@media (max-width: 375px) {
    .home {
        flex-direction: column-reverse;
    }
    .home .title {
        width: 95%;
        margin-left: 0;
        margin: 0 auto;
        top: 5%;
        position: relative;
    }
    .home .title h2 {
        font-size: 40px;
        text-align: center;
    }
    .home .title p {
        width: 95%;
        margin: 0 auto 25px;
    }
    .home .title button {
        margin: 0 25%;
    }
    .home .image {
        width: 100%;
        overflow: visible;
        background-image: url('../../../public/images/bg-intro-mobile.svg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: right;
        margin-bottom: -5%;
    }
    .home .image .mockups {
        position: relative;
        top: 0;
        left: 0;
        z-index: 0;
    }
}