:root {
    --Dark-Blue: hsl(233, 26%, 24%);
    --Lime-Green: hsl(136, 65%, 51%);
    --Bright-Cyan: hsl(192, 70%, 51%);
    --Grayish-Blue: hsl(233, 8%, 62%);
    --Light-Grayish-Blue: hsl(220, 16%, 96%);
    --Very-Light-Gray: hsl(0, 0%, 98%);
    --White: hsl(0, 0%, 100%);
}
.footer {
    height: 30%;
    background-color: var(--Dark-Blue);
    margin:3% auto 0;
    padding: 0 5%;
    display: flex;
    color: var(--White);
}
.footer .social-icons {
    width: 25%;
    margin:3% auto;
}
.footer .social-icons .logo {
    margin: 10% 0;
}
.footer .social-icons .logo img {
    filter: saturate(400%) brightness(700%) contrast(400%);
    cursor: pointer;
}
.footer .social-icons .icons img{
    margin: 3%;
    cursor: pointer;
}
.footer .social-icons .icons img:hover {
    filter: invert(63%) sepia(84%) saturate(450%) hue-rotate(81deg) brightness(96%) contrast(82%);
}
.footer .info {
    width: 25%;
    margin: auto;
}
.footer .info div {
    width: fit-content;
    margin: 5% 0;
    cursor: pointer;
}
.footer .career {
    width: 25%;
    margin: auto;
}
.footer .career div {
    width: fit-content;
    margin: 5% 0 ;
    cursor: pointer;
}
.footer .info div:hover ,
.footer .career div:hover {
    color: var(--Lime-Green);
}
.footer .request {
    width: 25%;
    margin:3% auto ;
}
.footer .request button {
    width: 60%;
    padding: 15px;
    margin: 10% auto;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 20px;
    color: var(--White);
    background-image: linear-gradient(to right, var(--Lime-Green), var(--Bright-Cyan));
}
.footer .request div {
    color: var(--Grayish-Blue);
}
@media (max-width:375px) {
    .footer {
        flex-direction: column;
        text-align: center;
    }
    .footer .social-icons {
        width: 90%;
    }
    .footer .info {
        width: 100%;
    }
    .footer .career {
        width: 100%;
        margin-bottom: 0;
    }
    .footer .info div,
    .footer .career div {
         margin:5% auto;
    }
    .footer .career div {
        margin-top: 0;
    }
    .footer .request {
        width: 100%;
    }
  
}